/*===== GOOGLE FONTS =====*/


/*===== VARIABLES CSS =====*/
:root{
  --header-height: 3rem;

  /*===== Colors =====*/

  --first-color: #cedaf3;
  --first-color-dark: #7b89c9;
  --first-color-darken: #4257b2;
  
  --white-color: #FCF8F8;
  
  /*===== Font and typography =====*/
  --body-font: 'Poppins', sans-serif;
  --big-font-size: 2.5rem;
  --normal-font-size: .938rem;
  
  /*===== z index =====*/
  --z-fixed: 100;
}

@media screen and (min-width: 768px){
  :root{
    --big-font-size: 5rem;
    --normal-font-size: 1rem;
  }
}

/*===== BASE =====*/
*,::before,::after{
  box-sizing: border-box;
}

body{
  margin: 3rem 0 0 0;
  margin: var(--header-height) 0 0 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  font-family: var(--body-font);
  font-size: .938rem;
  font-size: var(--normal-font-size);
  font-weight: 500;
}

h1,p,ul{
  margin: 0;
}

ul{
  padding: 0;
  list-style: none;
}

a{
  text-decoration: none;
}

img{
  max-width: 100%;
  height: auto;
}

/*===== LAYOUT =====*/
.bd-grid{
  max-width: 1024px;
  display: grid;
  grid-template-columns: 100%;
  grid-column-gap: 2rem;
  -webkit-column-gap: 2rem;
          column-gap: 2rem;
  width: calc(100% - 2rem);
  margin-left: 1rem;
  margin-right: 1rem;
}

header{
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  z-index: var(--z-fixed);
  background-color: #4257b2;
  background-color: var(--first-color-darken);
}

/*===== NAV =====*/
.nav{
  height: 3rem;
  height: var(--header-height);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

@media screen and (max-width: 768px){
  .nav__menu{
    position: fixed;
    top: 0;
    right: -100%;
    width: 70%;
    height: 100%;
    padding: 3.5rem 1.5rem 0;
    background: rgba(255,255,255,.3);
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    transition: .5s;
  }
}

.nav__close{
  position: absolute;
  top: .75rem;
  right: 1rem;
  font-size: 1.5rem;
  cursor: pointer;
}

.nav__item{
  margin-bottom: 2rem;
}

.nav__logo {
    font-size: 2rem;
    font-weight: 700;
    color: #FCF8F8;
    color: var(--white-color);
}

.nav__close, .nav__link, .nav__toggle{
  color: #cedaf3;
  color: var(--first-color);
}

.nav__link:hover{
  color: #fff;
}

.nav__toggle{
  font-size: 1.5rem;
  cursor: pointer;
}

/*=== Show menu ===*/
.show{
  right: 0;
}

/*FOOTER*/

footer i{
  font-size: 30px;
}

/* ===== MEDIA QUERIES =====*/
@media screen and (min-width: 768px){
  body{
    margin: 0;
  }
  
  .nav{
    height: calc(3rem + 1.5rem);
    height: calc(var(--header-height) + 1.5rem);
  }

  .nav__toggle, .nav__close{
    display: none;
  }

  .nav__list{
    display: -webkit-flex;
    display: flex;
  }

  .nav__item{
    margin-left: 3rem;
    margin-bottom: 0;
  }

  .home__container{
    height: 100vh;
    grid-template-columns: repeat(2, -webkit-max-content);
    grid-template-columns: repeat(2, max-content);
    grid-template-rows:  1fr;
    grid-row-gap: 0;
    row-gap: 0;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
  }
  
  .home__img{
    -webkit-order: 1;
            order: 1;
    width: 375px;
    height: 289px;
  }

  .home__img img{
    width: 375px;
  }
}

@media screen and (min-width: 1024px){
  .bd-grid{
    margin-left: auto;
    margin-right: auto;
  }

  .home__container{
    -webkit-justify-content: initial;
            justify-content: initial;
    grid-column-gap: 4.5rem;
    -webkit-column-gap: 4.5rem;
            column-gap: 4.5rem;
  }

  .home__img{
    width: 604px;
    height: 466px;
  }

  .home__img img{
    width: 604px;
  }
}

/* ===== MEDIA QUERIES for SUMMARY=====*/
@media (max-width: 753px) {
    .wrapper{
        max-width: 700px;
    }
    .wrapper .percent{
        width: calc(50% - 20px);
        margin-bottom: 20px;
    }
}

@media (max-width: 505px) {
    .wrapper{
        max-width: 500px;
    }
    .wrapper .percent{
        width: 100%;
    }
}
