body{
  background: url("./images/background.jpeg");
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90vh;
  text-align: center;
}

.container h1{
  font-size: 3rem;
  margin-bottom: 0.2rem;
  margin-top: 10rem;
}

.Hangman-word{
  letter-spacing: 1em;
  margin: 0.1em -1em 0.2em 0;
  font-size: 3rem;
  color: #4CAF50;
}

.Hangman-btns{
  display: inline-block;
  text-align: center;
  width: 270px;
  margin-top: 0;
}

p.Hangman-btns{
  margin-top: 20px;
}

.Hangman-btns button{
  text-align: center;
  font-size: 1.2rem;
  cursor: pointer;
  width: 30px;
  padding: 10px 20px;
  background-color:#4CAF50;
  color: white;
  border-width: 4px;
}

p.result.\$\{isWon.\?.\"win\".\:.\"\"\}.\$\{isGameOver.\?.\"lose\".\:.\"\"\} {
  font-size: 50px;
  font-weight: 100;
}

button.reset{
  width: 100px;
  font-size: 1.5rem;
  margin-top: 40px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  width: 30vh;
  
  background-color: #4CAF50;
  /* border: 10vh; */
  border-radius: 50vh;
  border-width: 4px;
  color: white;
  padding-top: 10px;
  padding-bottom: 10px;
  margin-bottom: 5vh;
}